*{
  margin: 0;
  padding: 0;
}

body{
  background-color: rgb(174, 174, 174);
}

main{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 100px 0;
  min-height: 70vh;

  overflow: hidden;

  &>div{
    background-color: rgb(0, 144, 144);
    margin: 0 5px;
    flex-grow: 1;
    overflow: hidden;

    width: 2em;

    &.focus{
      width: auto;
    }
  }

  section{
    overflow-y: auto;
    text-align: center;

    &>h1{
      color: rgb(0, 0, 38);
      font-size: 2rem;
    }

    article {
      margin: 40px 0;
    }
  }
}