header{
    position: fixed;
    display: flex;
    
    top: 0;
    
    width: 100%;
    height: 100px;

    background-color: black;
}

header>ul{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 100%;

    &>li{
        display: flex;
        flex-grow: 1;
        margin: 0 5px;
        &>button{
            flex-grow: 1;

            min-height: 4em;

            background-color: rgb(165, 42, 153);
            border: 2px solid rgb(143, 21, 131);
            color: aliceblue;

            font-size: 20px;
            padding: 10px 5px;

            :hover{
                background-color: rgb(143, 21, 131);
                border: 2px solid rgb(95, 10, 87);

                color: aqua;
            }
        }
    }
}

footer {
    position: fixed;
    display: flex;

    bottom: 0;

    height: 100px;
    width: 100%;

    background-color: rgba(73, 73, 73, 0.8);
}